.profile {
  margin: 120px 0 0 340px;
  display: flex;
  flex-flow: row nowrap;
  align-items: start;
  gap: 60px;
}

.navigation {
  min-width: 320px;
}

.link {
  text-decoration: none;
  color: #8585AD;
}

.link:hover, .link[aria-current="page"] {
  color: #F2F2F3;
}

.link:hover[aria-current="page"] {
  cursor: initial;
}

.button {
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  color: #8585AD;
}

.button:hover {
  color: #F2F2F3;
  cursor: pointer;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.listItem {
  min-height: 64px;
  display: flex;
  align-items: center;
}

.form {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  position: relative;
}

.loading {
  position: absolute;
  inset: 0;
  background-color: #8585AD33;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
}

.link {
  text-decoration: none;
}

.info {
  width: 0;
  min-width: 100%;
  opacity: 0.48;
}

.buttons {
  align-self: end;
}

.hidden {
  display: none;
}

.error {
  color: #ff6363;
}
